
input[type=submit] {
    color: var(--neutral-white);
    outline: none;
    border: 0;
    padding: 1rem 1.9rem;
    background: var(--primary-moderate-blue);
    border-radius: 0.4rem;
    font-weight: 700;
    align-self: start;
    transition: 100ms ease-in-out;
}

input[type=submit]:hover {
    cursor: pointer;
    opacity: 0.5;
}

.new-comment-form {
    display: flex;
    gap: 0.7rem;
    align-items: stretch;
    animation: come-in 1.5s ease-in-out;
    padding: 1rem 2rem;
    background: var(--neutral-white);
    border-radius: 0.7rem;
}

.new-comment-form > img {
    align-self: start;
    width: 2.5rem;
    aspect-ratio: 1;
}

textarea {
    padding: 0.5rem 1rem;
    resize: none;
    color: var(--neutral-grayish-blue);
    border: 2px solid var(--neutral-light-gray);
    height: 4em;
    border-radius: 0.4rem;
    font-size: 16px;
    flex: 1;
}

textarea:focus {
    outline: none;
    border: 2px solid var(--primary-moderate-blue);
}

.add-comment-section {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.add-comment-section > img {
    display: none;
    align-self: start;
    width: 2.5rem;
    aspect-ratio: 1;
}

@keyframes come-in {
    0%{
        opacity: 0;
        transform: translateY(-100%);
    }

    75%{
        opacity: 0;
        transform: translateY(-50%);
    }

    100%{
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 768px) {
    .new-comment-form {
        flex-direction: column;
        max-height: 11.5rem;
        padding: 0.5rem 2rem 0.5rem 2rem;
    }
    
    
    .new-comment-form > img {
        display: none;
    }
    
    .add-comment-section > img {
        display: initial;
    }
    
    .new-comment-form > input[type=submit] {
        align-self: flex-end;
    }
    
    .new-comment-form > textarea {
        min-height: 5rem;
        border-radius: 0.8rem;
    }
    
    textarea {
        border-radius: 0.8rem;  
    }

    input[type=submit] {
        padding: 1rem 1.9rem;
        font-size: 16px;
        border-radius: 0.5rem;
    }
}